import { Component } from '@angular/core';

@Component({
    template: require('./action-sheet-example.component.html'),
    styles: [require('./action-sheet-example.component.scss')]
})
export class ActionSheetExampleComponent {
    isShow: boolean = false;

    trigger(isShow: boolean) {
        this.isShow = isShow;
    }
}


