import { Component } from '@angular/core';

@Component({
    template: require('./collection-tab-example.component.html'),
    styles: [require('./collection-tab-example.component.scss')]
})
export class CollectionTabExampleComponent {
    viewIndex: number = 0;
    tabIndex: number = 0;

    setViewIndex(n: number) {
        this.viewIndex = n;
    }

    setTabIndex(n: number) {
        this.tabIndex = n;
    }
}


